.reservation-container {
    display: flex;
    flex-wrap: nowrap;
    min-width: 355px;
    background-color: #fff;
    flex-direction: column;
    border: 1px solid rgba(0, 0, 0, 0.12);
    padding: 15px;
    z-index: 3;
}

.reservation-amounts-container {
    display: flex;
    flex-direction: column;
    padding: 10px 5px;
    flex-wrap: nowrap;
}

.reservation-reserve-button {
    margin-top: 15px !important;
}
